import Vue from 'vue'
import Router from 'vue-router'
import TheLogin from '@/components/TheLogin'
import HomePage from '@/views/HomePage'
import ImageList from '@/views/ImageList'
import Container from '@/container/Container'

Vue.use(Router)

export default new Router({
  routes: [
    { path: '/container',
    name: 'Container',
    component: Container},
    {
      path: '/',
      name: 'TheLogin',
      component: TheLogin
    },
    {
      path: '/home',
      name: 'HomePage',
      component: HomePage
    },
    {
      path: '/imageList',
      name: 'ImageList',
      component: ImageList
    }
  ]
})



