<template>
    <div class="container-fluid">
      <div class="row justify-content-center">
        <div class="col-11 col-sm-6 col-lg-4 card">
          <div class="card-body">
            <el-form :model="ruleForm" :rules="rules"
          status-icon
          ref="ruleForm"
          label-position="left"
          label-width="0px"
          class="demo-ruleForm login-page">
              <h3 class="title">欢迎登录</h3>
              <el-form-item prop="username">
                  <el-input type="text"
                      v-model="ruleForm.username"
                      auto-complete="off"
                      placeholder="用户名"
                  ></el-input>
              </el-form-item>
                  <el-form-item prop="password">
                      <el-input type="password"
                          v-model="ruleForm.password"
                          auto-complete="off"
                          placeholder="密码"
                      ></el-input>
                  </el-form-item>
              <el-checkbox v-model="checked"
                  class="rememberme"
              >记住密码</el-checkbox>
              <el-form-item style="width:100%;">
                  <el-button type="primary" style="width:100%;" @click="handleSubmit" :loading="logining">登录</el-button>
              </el-form-item>
          </el-form>
          </div>
        </div>
      </div>
      <div class="air-balloon ab-1">
      </div>
      <div class="air-balloon ab-2">
      </div>
      <div class="air-balloon ab-3">
      </div>
    </div>
</template>

<script>
$(function(){
	airBalloon('div.air-balloon');
});
/*
@function 热气球移动随机数
*/
;function rand(mi,ma){   
	var range = ma - mi;
	var out = mi + Math.round( Math.random() * range);	
	return parseInt(out);
};	
/*
@function 获取屏幕范围
*/
function getViewSize(){
	var de=document.documentElement;
	var db=document.body;
	var viewW=de.clientWidth==0 ?  db.clientWidth : de.clientWidth;
	var viewH=de.clientHeight==0 ?  db.clientHeight : de.clientHeight;
	return Array(viewW,viewH);
}


/*
@function 热气球移动
*/
function airBalloon(balloon){
	var viewSize = [] , viewWidth = 0 , viewHeight = 0 ;
	resize();	
	$(balloon).each(function(){
		$(this).css({top: rand(40, viewHeight * 0.5 ) , left : rand( 10 , viewWidth - $(this).width() ) });
		fly(this);
	});	
	$(window).resize(function(){
		resize()
		$(balloon).each(function(){
			$(this).stop().animate({top: rand(40, viewHeight * 0.5 ) , left : rand( 10 , viewWidth - $(this).width() ) } ,1000 , function(){
				fly(this);
			});
		});
	});
	function resize(){
		viewSize = getViewSize();
		viewWidth = $(document).width();
		viewHeight = viewSize[1];
	}
	function fly(obj){
		var $obj = $(obj);
		var currentTop = parseInt($obj.css('top'));
		var currentLeft = parseInt($obj.css('left') );
		var targetLeft = rand( 10 , viewWidth - $obj.width() );
		var targetTop = rand(40, viewHeight /2 );
		/*求两点之间的距离*/
		var removing = Math.sqrt( Math.pow( targetLeft - currentLeft , 2 )  + Math.pow( targetTop - currentTop , 2 ) );		
		/*每秒移动24px ，计算所需要的时间，从而保持 气球的速度恒定*/
		var moveTime = removing / 24;
		$obj.animate({ top : targetTop , left : targetLeft} , moveTime * 1000 , function(){
			setTimeout(function(){
				fly(obj)
			}, rand(1000, 3000))
    })
  }
};
export default {
  data () {
    return {
      logining: false,
      ruleForm: {
        username: 'admin',
        password: '123456'
      },
      rules: {
        username: [{required: true, message: 'please enter your account', trigger: 'blur'}],
        password: [{required: true, message: 'enter your password', trigger: 'blur'}]
      },
      checked: false
    }
  },
  methods: {
    handleSubmit (event) {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          this.logining = true
          if (this.ruleForm.username === 'admin' &&
          this.ruleForm.password === '123456') {
            this.logining = false
            sessionStorage.setItem('user', this.ruleForm.username)
            this.$router.push({path: '/home'})
          } else {
            this.logining = false
            this.$alert('username or password wrong!', 'info', {
              confirmButtonText: 'ok'
            })
          }
        } else {
          console.log('error submit!')
          return false
        }
      })
    }
  }
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container-fluid{
  height:100%;
  text-align: center;
  background: url('../assets/login_bg1.jpg');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  -moz-background-size: 100% 100%;
}
@media screen and (max-width: 600px) { /*当屏幕尺寸小于600px时，应用下面的CSS样式*/
  .container-fluid {
      height:100%;
      text-align: center;
      background: url('../assets/login_bg1_phone.jpg');
      background-repeat: no-repeat;
      background-size: 100% 100%;
      -moz-background-size: 100% 100%;
    }          
  }
.card{
  background-color: #ffffff85;
  margin-top:10em;
}
.air-balloon{
  position:absolute;
  top:-100px;
  left:-100px;
  z-index:50;
  }
.air-balloon.ab-1{
  width:43px;
  height:57px;
  background:url('../assets/air-balloon-1.png') no-repeat;
  }
.air-balloon.ab-2{
  width:24px;
  height:31px;
  background:url('../assets/air-balloon-2.png') no-repeat;
  }
  .air-balloon.ab-3{
  width:64px;
  height:79px;
  background:url('../assets/air-balloon-3.png') no-repeat;
  background-size: 100% 100%;
  -moz-background-size: 100% 100%;
  }

</style>



