<template>
  <div class="container-fluid">
    <div class="top-back-bar" @click="back">
      <svg class="bi bi-arrow-left-short" width="3em" height="3em" viewBox="0 0 20 20" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
        <path fill-rule="evenodd" d="M9.854 6.646a.5.5 0 010 .708L7.207 10l2.647 2.646a.5.5 0 01-.708.708l-3-3a.5.5 0 010-.708l3-3a.5.5 0 01.708 0z" clip-rule="evenodd"></path>
        <path fill-rule="evenodd" d="M6.5 10a.5.5 0 01.5-.5h6.5a.5.5 0 010 1H7a.5.5 0 01-.5-.5z" clip-rule="evenodd"></path>
      </svg>
    </div>
    <div class="top-to" @click="toTop">
        <svg class="bi bi-arrow-bar-up" width="3em" height="3em" viewBox="0 0 20 20" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
          <path fill-rule="evenodd" d="M13.354 7.854a.5.5 0 000-.708l-3-3a.5.5 0 00-.708 0l-3 3a.5.5 0 10.708.708L10 5.207l2.646 2.647a.5.5 0 00.708 0z" clip-rule="evenodd"></path>
          <path fill-rule="evenodd" d="M10 12a.5.5 0 00.5-.5V5a.5.5 0 00-1 0v6.5a.5.5 0 00.5.5zm-6 2.75a.5.5 0 01.5-.5h11a.5.5 0 010 1h-11a.5.5 0 01-.5-.5z" clip-rule="evenodd"></path>
        </svg>
    </div>
    <div class="row justify-content-center container-div">
      <div class="card col-11 col-sm-6 col-lg-3 shadow-sm p-3 mb-5 bg-white rounded" v-for="item in imageLists">
        <img :src="item.path" class="card-img-top">
      </div>
    </div>
  </div>
</template>
<script>

 export default {
  data () {
    return {
      paramModule: this.$route.params.module,
      imageLists:[],
     moduleImageCount:[9,21,13,15,17,18,26,67,9,9,18,18]
    }
  },
  created: function () {
    // 页面刷新时保持值
    window.addEventListener("beforeunload",()=>{
      localStorage.setItem("paramModule",this.paramModule)
    })
    if(!this.paramModule){
       this.paramModule=localStorage.getItem('paramModule');
    }
    this.imageLists=[];
    var index=parseInt(this.paramModule.split('-')[1]);
    for(var i=1;i<this.moduleImageCount[index-1]+1;i++){
      this.imageLists.push({path:require('../assets/'+index.toString()+'-'+i+'.jpg')})
    }
  },
  methods: {
    back (event) {
     this.$router.push({path: '/home'})
    },
    toTop (event) {
     $(window).scrollTop(0);
    }
  }
}

</script> 
<style scoped>
.container-fluid{
  height:100%;
}
.card{
  margin:1em;
  padding-left: 0px;
  padding-right: 0px;
}
.mb-5, .my-5 {
     margin-bottom: 1rem!important;
}

.card-body {
    padding: 0rem;
    padding-top:5px;
}
.top-back-bar{
    position: fixed;
    z-index: 10;
    left: 0;
    top: 1rem;
    background-color:rgba(178, 182, 183, 0.5)

}
.top-to{
    position: fixed;
    z-index: 10;
    right:0;
    bottom: 2rem;
    background-color:rgba(178, 182, 183, 0.5)

}
</style>


