<template>
  <div class="container-fluid">
    <div class="top-to" @click="toTop">
        <svg class="bi bi-arrow-bar-up" width="3em" height="3em" viewBox="0 0 20 20" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
          <path fill-rule="evenodd" d="M13.354 7.854a.5.5 0 000-.708l-3-3a.5.5 0 00-.708 0l-3 3a.5.5 0 10.708.708L10 5.207l2.646 2.647a.5.5 0 00.708 0z" clip-rule="evenodd"></path>
          <path fill-rule="evenodd" d="M10 12a.5.5 0 00.5-.5V5a.5.5 0 00-1 0v6.5a.5.5 0 00.5.5zm-6 2.75a.5.5 0 01.5-.5h11a.5.5 0 010 1h-11a.5.5 0 01-.5-.5z" clip-rule="evenodd"></path>
        </svg>
    </div>
    <div class="row justify-content-center" style="padding:15px;">
    
      <div id="carouselExampleCaptions" class="carousel slide col-12 col-sm-10 col-lg-8" data-ride="carousel">
        <ol class="carousel-indicators">
          <li data-target="#carouselExampleCaptions" data-slide-to="0" class="active"></li>
          <li data-target="#carouselExampleCaptions" data-slide-to="1"></li>
          <li data-target="#carouselExampleCaptions" data-slide-to="2"></li>
        </ol>
        <div class="carousel-inner">
          <div class="carousel-item active">
            <img src="../assets/lun1.jpg" class="d-block w-100" alt="...">
            <div class="carousel-caption d-none d-md-block">
              <h5>启程</h5>
              <p>旅游不在乎终点，而是在意途中的人和事还有那些美好的记忆和景色。</p>
            </div>
          </div>
          <div class="carousel-item">
            <img src="../assets/lun2.jpg" class="d-block w-100" alt="...">
            <div class="carousel-caption d-none d-md-block">
              <h5>京都印象</h5>
              <p>说走就走，是人生最华美的奢侈，也是最灿烂的自由。</p>
            </div>
          </div>
          <div class="carousel-item">
            <img src="../assets/lun3.jpg" class="d-block w-100" alt="...">
            <div class="carousel-caption d-none d-md-block">
              <h5>台北</h5>
              <p>一辈子是场修行，短的是旅行，长的是人生。</p>
            </div>
          </div>
        </div>
        <a class="carousel-control-prev" href="#carouselExampleCaptions" role="button" data-slide="prev">
          <span class="carousel-control-prev-icon" aria-hidden="true"></span>
          <span class="sr-only">Previous</span>
        </a>
        <a class="carousel-control-next" href="#carouselExampleCaptions" role="button" data-slide="next">
          <span class="carousel-control-next-icon" aria-hidden="true"></span>
          <span class="sr-only">Next</span>
        </a>
      </div>

    </div>
    <div class="row justify-content-center container-div">
      <div class="card col-11 col-sm-6 col-lg-3 shadow-sm p-3 mb-5 bg-white rounded" v-for="item in cardList">
        <img :src="item.path" class="card-img-top">
        <div class="card-body">
          <h5 class="card-title">{{ item.title }}</h5>
          <p class="card-text">{{ item.text }}</p>
           <div class="card-body">
              <svg xmlns="http://www.w3.org/2000/svg" class="bi bi-geo" width="2em" height="2em" viewBox="0 0 20 20" fill="currentColor">
                <path d="M13 6a3 3 0 11-6 0 3 3 0 016 0z"/>
                <path d="M9.5 6h1v9a.5.5 0 01-1 0V6z"/>
                <path fill-rule="evenodd" d="M8.489 14.095a.5.5 0 01-.383.594c-.565.123-1.003.292-1.286.472-.302.192-.32.321-.32.339 0 .013.005.085.146.21.14.124.372.26.701.383.655.245 1.593.407 2.653.407s1.998-.162 2.653-.407c.329-.124.56-.259.701-.383.14-.125.146-.197.146-.21 0-.018-.018-.147-.32-.339-.283-.18-.721-.35-1.286-.472a.5.5 0 11.212-.977c.63.137 1.193.34 1.61.606.4.253.784.645.784 1.182 0 .402-.219.724-.483.958-.264.235-.618.423-1.013.57-.793.298-1.855.472-3.004.472s-2.21-.174-3.004-.471c-.395-.148-.749-.337-1.013-.571-.264-.234-.483-.556-.483-.958 0-.537.384-.929.783-1.182.418-.266.98-.47 1.611-.606a.5.5 0 01.595.383z" clip-rule="evenodd"/>
              </svg>{{item.map}}
              <router-link :to="{ name: 'ImageList', params: { module: item.id}}" class="card-link">| get more</router-link>
           </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
$(document).ready(function() {			
	var canvas = document.getElementById("c");
	var ctx = canvas.getContext("2d");
	var c = $("#c");
	var w,h;
	var pi = Math.PI;
	var all_attribute = {
		num:60,            			 // 个数
		start_probability:0.1,		     // 如果数量小于num，有这些几率添加一个新的     		     
		radius_min:1,   			     // 初始半径最小值
		radius_max:2,   			     // 初始半径最大值
		radius_add_min:.3,               // 半径增加最小值
		radius_add_max:.5,               // 半径增加最大值
		opacity_min:0.3,                 // 初始透明度最小值
		opacity_max:0.5, 				 // 初始透明度最大值
		opacity_prev_min:.003,            // 透明度递减值最小值
		opacity_prev_max:.005,            // 透明度递减值最大值
		light_min:40,                 // 颜色亮度最小值
		light_max:70,                 // 颜色亮度最大值
	};
	var style_color = find_random(0,360);  
	var all_element =[];
	window_resize();
	function start(){
		window.requestAnimationFrame(start);
		style_color+=.1;
		ctx.fillStyle = 'hsl('+style_color+',100%,97%)';
		ctx.fillRect(0, 0, w, h);
		if (all_element.length < all_attribute.num && Math.random() < all_attribute.start_probability){
			all_element.push(new ready_run);
		}
		all_element.map(function(line) {
			line.to_step();
		})
	}
	function ready_run(){
		this.to_reset();
	}
	ready_run.prototype = {
		to_reset:function(){
			var t = this;
			t.x = find_random(0,w);
			t.y = find_random(0,h);
			t.radius = find_random(all_attribute.radius_min,all_attribute.radius_max);
			t.radius_change = find_random(all_attribute.radius_add_min,all_attribute.radius_add_max);
			t.opacity = find_random(all_attribute.opacity_min,all_attribute.opacity_max);
			t.opacity_change = find_random(all_attribute.opacity_prev_min,all_attribute.opacity_prev_max);
			t.light = find_random(all_attribute.light_min,all_attribute.light_max);
			t.color = 'hsl('+style_color+',100%,'+t.light+'%)';
		},
		to_step:function(){
			var t = this;
			t.opacity -= t.opacity_change;
			t.radius += t.radius_change;
			if(t.opacity <= 0){
				t.to_reset();
				return false;
			}
			ctx.fillStyle = t.color;
			ctx.globalAlpha = t.opacity;
			ctx.beginPath();
			ctx.arc(t.x,t.y,t.radius,0,2*pi,true);
			ctx.closePath();
			ctx.fill();
			ctx.globalAlpha = 1;
		}
	}
	function window_resize(){
		w = window.innerWidth;
		h = window.innerHeight;
		canvas.width = w;
		canvas.height = h;
	}
	$(window).resize(function(){
		window_resize();
	});
	function find_random(num_one,num_two){
		return Math.random()*(num_two-num_one)+num_one;
	}
	(function() {
		var lastTime = 0;
		var vendors = ['webkit', 'moz'];
		for(var xx = 0; xx < vendors.length && !window.requestAnimationFrame; ++xx) {
			window.requestAnimationFrame = window[vendors[xx] + 'RequestAnimationFrame'];
			window.cancelAnimationFrame = window[vendors[xx] + 'CancelAnimationFrame'] ||
										  window[vendors[xx] + 'CancelRequestAnimationFrame'];
		}
	
		if (!window.requestAnimationFrame) {
			window.requestAnimationFrame = function(callback, element) {
				var currTime = new Date().getTime();
				var timeToCall = Math.max(0, 16.7 - (currTime - lastTime));
				var id = window.setTimeout(function() {
					callback(currTime + timeToCall);
				}, timeToCall);
				lastTime = currTime + timeToCall;
				return id;
			};
		}
		if (!window.cancelAnimationFrame) {
			window.cancelAnimationFrame = function(id) {
				clearTimeout(id);
			};
		}
	}());
	start();
});
export default {
  data () {
    return {
     cardList:[
       {id:'m-1',path:require('../assets/home-list1.jpg'),title:'我们的故事从这里开始',text:'2018年2月，小伙伴们的第一次一同出行，阳光正好，温暖明媚！',map:'富士五湖'},
       {id:'m-2',path:require('../assets/home-list2.jpg'),title:'关西游五人组',text:'2018年5月，关西游五人组出发，精美的古建筑，惬意的小鹿，疯狂的USJ...',map:'大阪 京都 奈良'},
       {id:'m-3',path:require('../assets/home-list3.jpg'),title:'东京湾',text:'2018年10月，秋风瑟瑟但抵不住出游的好心情，心之所向哪里都是天堂',map:'东京湾'},
       {id:'m-4',path:require('../assets/home-list4.jpg'),title:'深秋・银杏物语',text:'以前以为银杏的金黄只能是图片里的绚丽鲜明，没想到在温暖的阳光下如此耀眼，真实的幸福感爆棚！',map:'昭和纪念公园'},
       {id:'m-5',path:require('../assets/home-list5.jpg'),title:'京都红枫巡礼',text:'2018年11月底，说走就走的京都之行就像命中注定的缘分一般，美的叹为观止，永生难忘！',map:'京都'},
       {id:'m-6',path:require('../assets/home-list6.jpg'),title:'长野滑雪之行',text:'2019年1月，第二次滑雪之行，终于可以自由的外八体验飞一般的感觉了，爽！',map:'长野'},
       {id:'m-7',path:require('../assets/home-list7.jpg'),title:'高尾山の桜',text:'2019年3月，高尾山之行，一路欢声笑语，旅行不在乎目的地，而是和小伙伴们一起',map:'高尾山'},
       {id:'m-8',path:require('../assets/home-list8.jpg'),title:'台北・吃货天堂',text:'2018年7月，由东京的初夏直接过渡到台北的酷暑，依然是吃货们的幸福天堂',map:'台北'},
       {id:'m-9',path:require('../assets/home-list9.jpg'),title:'横须贺',text:'旅游不在乎终点，而是在意途中的人和事还有那些美好的记忆和景色。',map:'横须贺'},
       {id:'m-10',path:require('../assets/home-list10.jpg'),title:'野反ダム',text:'说走就走，是人生最华美的奢侈，也是最灿烂的自由。',map:'吾妻郡'},
       {id:'m-11',path:require('../assets/home-list11.jpg'),title:'东京德国村',text:'阳光正好，我们在路上，前方有大海和远方，星辰和灯光。',map:'千叶县'},
       {id:'m-12',path:require('../assets/home-list12.jpg'),title:'奥多摩',text:'2019年的秋，最好的时光在路上。跨过山和大海，也穿过人山人海',map:'奥多摩'},
     ]
    }
  },
  methods: {
    back (event) {
     this.$router.push({path: '/home'})
    },
    toTop (event) {
     $(window).scrollTop(0);
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.container-fluid{
  height:100%;
  text-align: center;
  background: url('../assets/home-bg.png');
  background-repeat: no-repeat;
  background-size: 100% 100%;
  -moz-background-size: 100% 100%;
}
@media screen and (max-width: 600px) { /*当屏幕尺寸小于600px时，应用下面的CSS样式*/
  .container-fluid {
      height:100%;
      text-align: center;
      background: url('../assets/home-bg-phone.png');
      background-repeat: no-repeat;
      background-size: 100% 100%;
      -moz-background-size: 100% 100%;
    }          
  }
.container-div{
  margin-top:10px
}
.card{
  margin:1em;
  padding-left: 0px;
  padding-right: 0px;
}
.mb-5, .my-5 {
     margin-bottom: 1rem!important;
}

.card-body {
    padding: 0rem;
    padding-top:5px;
}
.top-to{
    position: fixed;
    z-index: 10;
    right:0;
    bottom: 2rem;
    background-color:rgba(178, 182, 183, 0.5)

}
</style>


